import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";

@Component({
  selector: "coachs",
  templateUrl: "coachs.page.html",
  styleUrls: ["coachs.page.scss"]
})
export class CoachsPage implements OnInit {
  public coachsList: [];

  constructor(private router: Router) {}

  async ngOnInit() {
    this.coachsList = await this.getSampleData();
  }

  getItem(coach) {
    this.router.navigate(["/coachDetail"], { state: { data: { coach } } });
  }

  async getSampleData() {
    const sampleData = await fetch(
      "https://jsonplaceholder.typicode.com/users"
    );
    const parsedData = await sampleData.json();
    return parsedData;
  }
}
